*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.main{
  display: grid;
  grid-template-columns: 240px 1680px;
}
.left{
  background-color:#091336;
  min-height: 100vh;
}
.logo{
  margin-top: 20px;
  margin-left: 12px;
  width: 109px;
  height: 28px;
}
.right{
  min-height: 100vh;
  background-color:#E5E5E5;
}
.head_info{
  height: 64px;
background-color:#FFFFFF;
display: flex;
justify-content: center;
align-items:center;
box-shadow: 0px 4px 5px #E9EDF3;
}
.head_info_container{
  width: 1440px;
  display: flex;
  align-items:center;
}
.date{
  width: 90px;
  display: inline-block;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  color: #899CB1;
}
.indicators{
  display: flex;
  width: 594px;
  height: 10px;
  margin-left: 86px;
  align-items:center;
  justify-content: space-between;
  font-style: normal;
font-weight: 400;
}
.basic_search{
  width: 32px;
  height: 48px;
  margin-left: 189px;
  display: flex;
  align-items:center;
  justify-content: center;
}
.basic_search img{
  width: 16px;
  height: 16px;
}
.user_name{
  color:#899CB1;
  margin-left: 40px;
  font-size: 15px;
  font-weight: 400;
  
}
.vector_down{
  margin-left: 4px;
  width: 24px;
  display: flex;
  align-items:center;
  justify-content: center;
}
.vector_down img{
  width: 12px;
}
.avatar{
  margin-left: 41px;
  text-align: center;
}
.avatar img{
  border-radius: 50%;
}
.new_calls_text{
  font-size: 14px;
}
.quality_text{
  font-size: 14px;
}
.conversion_text{
  font-size: 14px;
}
.bar{
  margin-top: 7px;
  height: 6px;
  background: #DEE6F5;
  border-radius: 12px;
}
.bar_calls{
  height: 100%;
  width: 68px;
  background: #28A879;
  border-radius: 12px 0px 0px 12px;
}
.bar_quality{
  height: 100%;
  width: 67px;
  background: #FFD500;
  border-radius: 12px 0px 0px 12px;
}
.bar_conversion{
  height: 100%;
  width: 78px;
  background: #EA1A4F;
  border-radius: 12px 0px 0px 12px;
}
.new_calls_text span{
  color:#00A775;
}
.quality_text span{
  color:#FFB800;
}
.conversion_text span{
  color:#EA1A4F;
}
.menu_item{
  width:100%;
  height: 52px;
  font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 24px;
color:rgba(255, 255, 255, 0.6);
display: flex;
padding: 0 12px;
align-items: center;
}
.menu_item span{
  width: 170px;
  padding: 15px;
}
.menu_item img{
  width:15px;
}
.active{
  border-left: 3px solid #002CFB;
  background-color: rgba(216, 228, 251, 0.32);
}
.active::after{
  content:'';
  border-radius: 50%;
  width: 8px;
  height: 8px;
  background-color: #FFD500;
  box-shadow: 0px 3px 8px rgba(237, 218, 1, 0.5);
  display: inline-block;
}
.left_menu{
  margin-top: 32px;
}
.button_menu{
  width: 200px;
  height: 52px;
  background: #005FF8;
  border-radius: 4px;
  border:none;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: #FFFFFF;
  margin: 0 auto;
  display: grid;
  padding: 0 20px;
  grid-template-columns: 1fr 25px;
  justify-items: center;
  align-items:center;

}
.button_menu:first-of-type{
  margin-top: 64px;
}
.button_menu:last-child{
  margin-top: 32px;
}
.ballance_view{
  width: 145px;
height: 40px;
background: #FFFFFF;
border-radius: 48px;
}
.left_calendar{
  margin-left: 48px;
  margin-right: 22px;
  transform: rotate(90deg);
}
.right_calendar{
  margin-left: 22px;
  transform: rotate(270deg);
}
.ballance_info{
  display: flex;
  justify-content: end;
  align-items:center;
  margin-top: 20px;
}
.ballance_view{
  display: flex;
  justify-content: space-around;
  align-items:center;
  padding: 0 8px;
  color: #899CB1;
}
.money_count{
  font-style: normal;
font-weight: 500;
font-size: 14px;
color: #122945;
}
.main_screen{
  width: 1440px;
  margin: 0 auto;
}
.ballance_calendar{
  display: flex;
  align-items: center;
}
.calendar_days{
  margin-left: 8px;
  font-style: normal;
font-weight: 400;
font-size: 14px;
color: #005FF8;
}
.filters{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 34px;
}
.right_filter_group{
  display: flex;
  justify-content: center;
  align-items:center;
  font-style: normal;
font-weight: 400;
font-size: 14px;
color: #5E7793;
}
.clearfix_filter_right{
  margin-left: 36px;
}
.icon_right{
  margin-right: 8px;
}
.search_phones{
  display: flex;
  align-items:center;
  font-style: normal;
font-weight: 400;
font-size: 14px;
color: #5E7793;
}
.search_phones img{
  margin-right: 12px;
}
.calls{
  width: 100%;
  padding-left: 40px;
  background-color:#FFFFFF;
  box-shadow: 0px 4px 5px #E9EDF3;
  border-radius: 8px;
  margin-top: 20px;

}
.calls_head{
  display: grid;
  grid-template-columns: 1fr 89px 128px 326px 214px 291px 291px;
  grid-template-rows: 60px;
  font-style: normal;
font-weight: 400;
font-size: 14px;
color: #899CB1;
}
.durations{
  display: flex;
  justify-content: end;
  align-items:center;
  padding-right: 40px;
}
.grade_call{
 display: flex;
 align-items:center;
}
.source_call{
  display: flex;
 align-items:center;
}
.call_numbers{
  display: flex;
  align-items:center;
}
.employee_call{
  display: flex;
  align-items:center;
}
.time_call{
  display: flex;
  align-items:center;
}
.type_call{
  display: flex;
  align-items:center;
}
.down_tool{
  width: 100%;
  height: 2px;
  background-color:#EAF0FA;
}
.calls_body{
  display: grid;
  grid-template-columns: 1fr 89px 128px 326px 214px 291px 291px;
  grid-template-rows: 60px;
  font-style: normal;
font-weight: 400;
font-size: 14px;
color: #899CB1;
}